
#app {
  font-family: 'Droid Serif';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}
.legend {
  -webkit-box-shadow: 0 0 10px rgba(0,0,0,0.3);
          box-shadow: 0 0 10px rgba(0,0,0,0.3);
  padding-top: 250px;
  position: absolute;
  right: 10px;
  top: 10px;
  width: 200px;
  z-index: 5;
}
.legend h1 {
  background-color: rgba(0,0,0,0.3);
  border-left-color:  rgba(0,0,0,0.3);
  border-left-style: solid;
  border-left-width: 10px;
  font-size: 16px;
  margin: 0;
  padding: 10px;
}
.legend span {
  color: #fff;
  font-size: 16px;
  font-weight: bold;
}
.legend p {
  background-color: rgba(0,0,0,0.8);
  color: #ccc;
  font-size: 14px;
  margin: 0;
  padding: 10px 0px 10px 20px;
}
.loader {
  -webkit-animation: fadeOut 3s ease-in 1 forwards;
  animation: fadeOut 3s ease-in 1 forwards;
  background-color: #18263b;
  clip: none;
  height: 100%;
  margin: 0;
  opacity: 1;
  position: relative;
  width: 100%;
  z-index: 5;
}
.lds-ripple {
  display: block;
  height: 64px;
  left: 50%;
  position: absolute;
  top: 50%;
  width: 64px;
  z-index: 5;
}
.lds-ripple div {
  -webkit-animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
          animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
  border-radius: 50%;
  border: 4px solid #ccc;
  opacity: 1;
  position: absolute;
}
.lds-ripple div:nth-child(2) {
  -webkit-animation-delay: -0.5s;
          animation-delay: -0.5s;
}
@-webkit-keyframes fadeOut {
0% {
    opacity: 1;
}
99% {
    opacity: 0;
}
100% {
    position: absolute;
    clip: rect(0px, 0px, 0px, 0px);
}
}
@keyframes fadeOut {
0% {
    opacity: 1;
}
99% {
    opacity: 0;
}
100% {
    position: absolute;
    clip: rect(0px, 0px, 0px, 0px);
}
}
@-webkit-keyframes lds-ripple {
0% {
    top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1;
}
100% {
    top: -1px;
    left: -1px;
    width: 58px;
    height: 58px;
    opacity: 0;
}
}
@keyframes lds-ripple {
0% {
    top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1;
}
100% {
    top: -1px;
    left: -1px;
    width: 58px;
    height: 58px;
    opacity: 0;
}
}
.is-paused {
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}

